<template>
  <div id="app">
    <router-view/>
  </div>
</template>
<script>
export default {
  created() {
    this.logWinSize(window.innerWidth)
    window.addEventListener('resize', this.resizeHandler)
  },
  beforeDestroy() {
    window.removeEventListener('resize', this.resizeHandler)
  },
  methods: {
    logWinSize(size) {
      this.$store.commit('setWinSize', size)
    },
    resizeHandler(e) {
      this.logWinSize(window.innerWidth)
    }
  }
}
</script>
